*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body{
  width: 100%;
}
.sticky{
  position: sticky;
  top: 0;
  z-index: 50;
}
.header{
  width: 100%;
  height: 120px;
  background-color: rgb(0, 15, 46); 
  border-bottom: 3px solid  #F95700FF;
}
.footer{
  padding: 1% 2% 1% 2%;
  margin-top: 2%;
  width: 100%;
  height: 100%;
  top: 100%;
  bottom : 0;
  background-image: linear-gradient(to left, rgb(56, 43, 1), rgb(20, 20, 20)); 
}
.footerheadingtext{
  margin: -25px 0 0 0;
}
.footer img {
  margin-left: 0%;
  height : 150px;
  width : 200px;
}
.footerrighttext{
    color: white;
    margin: -180px 1% 0% 50%;
}
.heading{
    padding: 35px 0px 0px 1%;
    transition-duration: 0.5s;
}

.heading:hover{
  text-shadow: 10px 0px 35px #ffffff;
	animation: glow 1s ease-in-out infinite alternate;
  color: rgb(0, 0, 0);
  transition-duration: 0.5s;
}

.beforenav ul li{
  font-weight: bolder;
  list-style: none;
  float: right;
  margin: -2% 1% 0px 0px;
  color:  whitesmoke;
  padding: 1%;
  border-right: 2px solid #F95700FF;
  transition-duration: 0.5s;
}
#afternav {
  position: absolute;
  right : 7%;
  top : 100px;
  width: 30%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.897);
  padding: 1%;
  display: none;
}

#afternav ul a{
  color: white;
  text-decoration: none;
  font-weight: bolder;
  list-style: none;
}
#afternav ul a li{
  padding: 2%;
}
#afternav ul a li:hover{
  background-color: saddlebrown;
}
.beforenav ul li:hover{
  transition-duration: 0.5s;
  color: #f83600;
}

.nav{
  color: aliceblue;
  float: right;
  margin: -1% 9% 0% 1%;
  cursor: pointer;
  opacity: 0;
}
.nav:hover{
  color: #f83600;
}
.beforenav ul #contact{
  border-right: none;
}

#box{
  height: 520px;
  width: 95%;
  display: block;
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
  margin: auto;
}

#overslideshow{
  transition-duration: 1s ease;
  opacity: 1;
  padding: 5%;
  transition: .5s ease;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 5%;
}
#swagatam{
  background-image: linear-gradient(to bottom left, rgb(51, 139, 228),#061161);
  padding: 5%;
  border: 3px solid rgb(255, 255, 255);
  color: whitesmoke;
  cursor: pointer;
  transition-duration: 0.5s;
  opacity: 0;
}
#swagatam:hover{
  transform: scale(1.15,1.15);
  
}
#swagatamtext{
  color: white;
  margin-top: 1%;
  height: 300px;
  transition-duration: 1s;
  opacity: 0;
  padding: 5% 5% 5% 5%;
  text-align: center;
  margin-top: 0%;
  background-color: rgba(0, 0, 0, 0.836);
}

.movebelowbutton{
  padding: 1%;
  background-color: rgb(255, 255, 255);
  width: 30%;
  transition-duration: 0.2s;
  color: rgb(0, 0, 0);
  margin-left: 35%;
}
.movebelowbutton #gif {
  transform: rotate(90deg);
  height: 30px;
  width: 40px;
  filter: invert(100%);
}

.movebelowbutton:hover {
  background-color: rgb(0, 0, 0);
  color: #ffffff;
  transition-duration: 0.2s;
}

.movebelowbutton:hover #gif{
 filter : invert(100%);
  transition-duration: 0.2s;
}

.linkinimage{
  font-size: 70%;
  text-align: left;
  color: rgb(0, 0, 0);
  text-decoration: none;
}
.linkinimage li{
  margin-top: 1%;
}
.linkinimage li:hover{
  color: #f83600;
}

.imgrow1 {
  display: flex;
  flex-wrap: wrap;
}

.imgcolumn1 {
  width: 25%;
  padding: 0px 20px;
}

.imgcolumn1 img {
  margin-top: 5%;
  vertical-align: middle;
  width: 100%;
  filter: grayscale(0%);
  transition-duration: 0.5s;
  overflow: hidden;
  height: 200px;
}
.overlaya1{
  background: rgba(255, 255, 255, 0.9);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity:1;
  font-size: 20px;
  padding: 20px;
  text-align: left;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
} 
#welcometext{
  height: 100%;
  margin: -90px 5% 5% 5%;
}
.services{
  background-image: url(background.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.rightservices{
    padding : 1%;
    line-height: 37px;
    width: 60%;
    float: left;
    display: block;
  
    height: 100%;
    color: #00203FFF
}
.fitems{
  margin-top: 30px;
}

.row {
  width: 50%;
  display: inline-block;
  }
  
  .col-50 { 
    flex: 50%;
  }
  
  .col-75 {
    width:  100%;
  }

  .container {
    height: 100%;
    width: 40%;
    display: inline-block;
    font-weight: bolder;
    color: rgb(255, 255, 255);
    background-image: linear-gradient(to left, rgb(0, 128, 255),#152cfc);
    padding: 5px 20px 15px 20px;
    border: 1px solid lightgrey;
    border-radius: 3px;
  }
  
  input[type=text] {
    width:100%;
