    padding: 12px;
    border: 1px solid #F95700FF;
    border-radius: 3px;
  }
  #servi{
    text-align: center;
    transition-duration:0.5s ; 
    padding: 4%; 
    background-image: linear-gradient(to top left, #fe8c00, #f83600); 
    color: whitesmoke;
  }
  label {
    margin-bottom: 10px;
    display: block;
  }
  .cols{
    column-width: 50%; columns: 2;
  }  
  button{
    display: none;
  }
  
  .contactbox{
    z-index: 0;
    float: left;
    display: block;
    height: 500px;
    width: 50%;
    color: azure;
    background-image: linear-gradient(to bottom left, rgb(51, 139, 228),#061161);
  }
  .cform{
    height: 500px;
    float: right;
    width: 49.5%;
    display: flex;
    z-index: 5;
    right: 0;
    position: absolute;
    font-weight: bolder;
    color: #00203FFF;
    background-color: rgb(173, 239, 228);
    padding: 5px 20px 15px 20px;
    border: 1px solid lightgrey;
    border-radius: 3px;
   }
  .logoc{
    width: 200px;
    display: inline-block;
    margin-left: 34%;
  }
  .tui{
    margin: -30px 0px 30px 27%;
  }
  .location{
    margin: 1% 3% 0% 12%;
    padding: 5%;
    line-height: 20px;
    overflow: hidden;
  }
  .shine {
    position: absolute;
    top: 0;
    z-index: 1;
    overflow: hidden;
    margin-top: 9%;
    left: -9%;
    height: 500px;
    width: 100px;
    background: rgba(255, 255, 255, 0.4);
    transition: all .2s linear;
    transform: skewX(40deg) translateX(0);
  }
  .contactbox:hover .shine {
    transform: skewX(40deg) translateX(700px);
    z-index: 1;
  }

 .cform{
  margin-top: 0.1%;
  height: 500px;
