  }

.gmap_canvas {
  padding: 1%;
  overflow:hidden;
  height:600px;
  width:100%;
}
#breaker{
  background-image: url(breaker.jpg);
  padding: 4%;
  background-repeat: no-repeat;
  background-attachment: fixed;
 
}

.imgrow {
  display: flex;
  flex-wrap: wrap;
  padding: 14px 14px 14px 14px;
}

.imgcolumn {
  flex: 50%;
  width: 50%;
  padding: 0px 20px;
}

.imgcolumn img {
  margin-top: 5%;
  vertical-align: middle;
  width: 100%;
  filter: grayscale(0%);
  transition-duration: 0.5s;
  overflow: hidden;
}

.overlay{
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity:0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
.overlaya{
  position: absolute;
  bottom: 0%;
  background: rgba(0, 0, 0, 0.9);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity:1;
  font-size: 20px;
  padding: 10px;
  text-align: center;
}
.overlaycontainer{
  position: relative;
  overflow: hidden;
}
.img9{
  width: 100%;
}
.overlaycontainer:hover img{
  transform: scale(1.1,1.06);
  transition-duration: 0.5s;
  filter: grayscale(70%);
}

.overlaycontainer:hover .overlay{
  opacity: 1;
}

.tbi{
  background-image: linear-gradient(to right, rgb(0, 0, 0),#00031b);
  text-align: center;
  padding: 3%;
