  transition-duration: 1s;
  background-image: linear-gradient(to right,#050044,rgb(41, 28, 0));
  box-shadow: 5px 24px 20px 0 rgba(26, 26, 26, 0.822);
  margin: 2% 3% 5% 5%;
}
.profile1:hover {
  transform: scale(1.05,1.05);
  transition-duration: 1s;
}
.profilephoto{
  display: block;
  float: left;
